import request from '@/utils/request'

export function bookLogin(data) {
  return request({
    url: '/login',
    method: 'post',
    data
  })
}

//
export function bookList(data) {
  return request({
    url: 'book/list',
    method: 'post',
    data
  })
}

//
export function bookAdd(data) {
  return request({
    url: 'book/add',
    method: 'post',
    data
  })
}

//
export function bookUpdate(data) {
  return request({
    url: 'book/update',
    method: 'post',
    data
  })
}

//
export function bookDelete(id) {
  return request({
    url: `book/delete/${id}`,
    method: 'post',   
      id
  })
}

//
export function userBatchDelete(data) {
  return request({
    url: 'book/delete/batch',
    method: 'post',
    data
  })
}



